<template>
    <div class="container">
        <h3> </h3>
        <div class="info">
            <div style="width: 85%; margin: auto; padding: 20px;">
                <h6 style="color: #cccccc;">   .</h6>
                <el-form label-width="120px">
                    <el-form-item label="">
                        <el-input style="width:300px" v-model="state.userid" :ref = "el => {form[0] = el}"/>
                    </el-form-item>
                    <el-form-item label="">
                        <div class="demo-date-picker">
                            <el-date-picker type="date" v-model="state.birth" 
                                placeholder=" "
                                style="width:300px; float: right;"
                                format="YYYY/MM/DD"
                                value-format="YYYYMMDD"
                                :ref = "el => {form[1] = el}" />
                        </div>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input style="width:90px" v-model="state.phone" :ref = "el => {form[2] = el}"/>
                      <label>-</label>
                      <el-input style="width:100px" v-model="state.phone1" :ref = "el => {form[3] = el}"/>
                      <label>-</label>
                      <el-input style="width:100px" v-model="state.phone2" :ref = "el => {form[4] = el}"/>
                    </el-form-item>
                </el-form>
                <el-button round @click="handleSendMail()" style="float: right; margin-right: 110px; font-size: 15px; font-weight: 600;"> </el-button>
            </div>
            <br />
            <div v-if="state.sendresult == 1" style="width: 75%; margin: auto; padding: 20px;">
                <hr />
                <el-form-item>
                    <h6 style="color: #cccccc;">   .</h6>
                    <el-input style="width:300px" v-model="state.userinputcode" :ref = "el => {form[5] = el}"/>
                    <el-button round @click="handleCodeCheck()" style="margin-left: 20px; font-size: 15px; font-weight: 600;" > </el-button>
                </el-form-item>
                <hr />
            </div>
            <div v-if="state.codeckeckret == 1" style="margin: auto; width: 75%;">
                <el-form :model="form" label-width="120px">
                    <el-form-item label="* ">
                        <el-input type="password" style="width:200px" v-model="state.newpw" :ref = "el => {form[6] = el}"/><br />
                    </el-form-item>
                        
                    <el-form-item label="*  ">
                        <el-input type="password" style="width:200px" v-model="state.newpw1" :ref = "el => {form[7] = el}"/>
                        <el-button round @click="handleSetPw()" style="margin-left: 20px; font-size: 15px; font-weight: 600;" > </el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive, ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
export default {
    setup () {
        const router = useRouter();
        const form = ref([]);
        const state = reactive({
            userid : '',
            birth  : '',
            phone  : '',
            phone1 : '',
            phone2 : '',
            sendresult : 0, //  input  v-if  
            mailcode : '', //   
            userinputcode : '', //   
            codeckeck : '  ', //   
            codeckeckret : 0, //   ( 1 )
            newpw : '', //  
            newpw1 : '' //   
        });

        //    
        const handleSendMail = async() => {
            if(state.userid ===''){
                alert(' ')
                form.value[0].focus();
                return false;
            }
            if(state.birth ===''){
                alert(' ')
                form.value[1].focus();
                return false;
            }
            if(state.phone ===''){
                alert(' ')
                form.value[2].focus();
                return false;
            }
            if(state.phone1 ===''){
                alert(' ')
                form.value[3].focus();
                return false;
            }
            if(state.phone2 ===''){
                alert(' ')
                form.value[4].focus();
                return false;
            }
            const url = `/fligent/api/member/sendmail.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                userid  : state.userid,
                birth   : state.birth,
                phone   : `${state.phone}-${state.phone1}-${state.phone2}`
            }
            const {data} = await axios.post(url, body, {headers});
            // console.log(data);
            if(data.status === 200){
                alert(' [ '+ state.userid + ' ]  !\n  ');
                state.sendresult = 1;

                //     
                state.mailcode = data.code;
                // console.log("  !!!!!!!!!!! data => ", data.code)
                // console.log("   => ",state.mailcode);
            } else {
                alert('   ')
            }
        }   

        //        
        const handleCodeCheck = () => {
            if(state.userinputcode ===''){
                alert(' ')
                form.value[5].focus();
                return false;
            }
            if(state.mailcode == state.userinputcode){ //        
                alert(' !\n  ')
                state.codeckeckret = 1;
            } else {
                alert('  \n  ')
            }
        }

        //  
        const handleSetPw = async() => {
            //   
            // newpw  newpw1  
            // newpw  newpw1  
            if(state.newpw ===''){
                alert('  ')
                form.value[6].focus();
                return false;
            }
            if(state.newpw1 ===''){
                alert('   ')
                form.value[7].focus();
                return false;
            }
            if(state.newpw != state.newpw1){
                alert('    ')
                form.value[6].focus();
                return false;
            }
            
            const url = `/fligent/api/member/setnewpw.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                userid  : state.userid,
                userpw  : state.newpw
            }
            const { data } = await axios.put(url, body, {headers});
            // console.log("@@@@@@@@@@2  !!!!!!!!!!! data => ", data );
            if(data.status == 200){
                alert('  !\n  ')
                router.push({path:'/memberlogin'});
            } else {
                alert(' !  ')
                router.go(0);
            }
        }

        return {
            state,
            form,
            handleSendMail,
            handleCodeCheck,
            handleSetPw
        }
    }
}
</script>

<style lang="css" scoped>
.container{
    width: 50%;
    height: 500px;
    /* border:1px solid #cccccc; */
    text-align: center;
}
.container h3 {
    margin-top: 100px;
    margin-bottom: 50px;
    margin-left: 50px;
    font-family: 'MapoFlowerIsland';
    font-size: 40px;
    font-weight: 600;
}
.info{
    margin: auto;
    /* border:1px solid #cccccc; */
    width: 80%;
    text-align: center;
}
