<template>
    <div>
    </div>
</template>

<script>
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
const kakaoHeader = {
    'Content-type': 'application/x-www-form-urlencoded;charset=utf-8'   
};
const getKakaoToken = async (code) => {
    try {
        const data = {
            grant_type: 'authorization_code',
            client_id: '0cd4b2c06665d59daf1522e2a28e0bb3',
            // redirect_uri: 'http://localhost:9090/kakao_callback', // vue    URI
            redirect_uri: 'http://1.234.5.158:28080/fligent/kakao_callback', //    URI
            code: code
        };
        const queryString = Object.keys(data)
            .map(k => encodeURIComponent(k) + '=' + encodeURIComponent(data[k]))
            .join('&');
        const result = await axios.post('https://kauth.kakao.com/oauth/token', queryString, {kakaoHeader});
        return result;
    } catch (e) {
        return e;
    }
};

const getKakaoUserInfo = async () => {
    let data = '';
    await window.Kakao.API.request({
        url: "/v2/user/me",
        success: function (response) {
            data = response;
        },
        fail: function (error) {
            console.log(error);
        },
    });
    return data;
}

var code = '';
export default {
    created() {
        if (this.$route.query.code) {
            code = this.$route.query.code;
            this.setKakaoToken();
        }
    },
    setup () {
        // const route = useRoute();
        const router = useRouter();
        const store = useStore();

        const state = ({
            userid    :'',
            userpw    :'',
            role      : 'CUSTOMER',
            birth     :'', // DB  
            nickname  :'',
            phone     :'',
            apiimageurl : ''
        })
        
        const setKakaoToken = async() => {
            const { data } = await getKakaoToken(code);
            if (data.error) {
                alert('  .');
                // this.$router.replace('/login');
                // router.push({ path: '/' });
                return;
            }
            window.Kakao.Auth.setAccessToken(data.access_token);
            await setUserInfo();
            // router.push({ path: '/' });
        }

        const setUserInfo = async() => {
            const res = await getKakaoUserInfo();
            // ()
            if(res.kakao_account.email != undefined){
                state.userid = res.kakao_account.email;
            }
            else{
                state.userid = res.id;
            }
            //  
            // 
            state.nickname = res.kakao_account.profile.nickname;
            //  
            state.apiimageurl = res.kakao_account.profile.profile_image_url;
            //  
            state.phone = '000-0000-0000';
            // 
            if(res.kakao_account.birthday != undefined){
                state.birth = '1933'+res.kakao_account.birthday;
            }
            else{
                state.birth = '330101';
            }
            kakaoUseridCheck();
        }

      
        //  ()  
        const kakaoUseridCheck = async() => {
            const url = `/fligent/api/member/idcheck.json?userid=${state.userid}`;
            const headers = {"Content-Type":"application/json"};
            const { data } = await axios.get(url,{headers});

            if(data.result === true){ //DB    
                kakaoUserLogin() //    
            } else { //DB     
                kakaoUserJoin(); //   
            }
        }



        //  
        const kakaoUserJoin = async() => {
            const url = `/fligent/api/member/join.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                userid : state.userid,
                userpw : state.userpw,
                role : state.role,
                birth : state.birth,
                nickname : state.nickname,
                phone : state.phone,
                airportname:'GMP'
            }
            const { data } = await axios.post(url, body, {headers});
            if(data.status === 200){
                alert('');
            }
            await kakaoUserJoinImg();
        }

        //    
        const kakaoUserJoinImg = async() => {
            const url = `/fligent/api/memberimage/apiinsert.json`;
            const headers = {"Content-Type":"application/json"};
            
            const member1 = {
            userid : state.userid
            }
            const body = {
            member : member1,
            apiimageurl : state.apiimageurl
            }
            const response = await axios.post(url, body, {headers});
            if(response.status == 200){
                kakaoUserLogin();
            }
            else{
                alert("  ")
            }
        }

        //  + 
        const kakaoUserLogin = async() => {
            const url = `/fligent/api/member/login.json`;
            const headers = { "Content-Type": "application/json" };
            const body = {
                    userid: state.userid,
                    userpw: state.userpw,
                    role: 'CUSTOMER'
                }
            const { data } = await axios.post(url, body, { headers });
                if (data.status === 200) {
                    sessionStorage.setItem("token", data.result);

                    store.commit('setLogged', true);
                    store.commit('setCounter');
                    store.commit('setImageurl', state.apiimageurl);

                    router.push({ path: '/' });
                }
        }
        return {state, setKakaoToken, setUserInfo}
    },
}
</script>

