<template>
    <div class="container">
        <h3></h3>
        <hr />

        <div class="order">
            <el-form :model="form" label-width="120px" style="width: 45%; margin:0 auto;">
                <h4>*</h4>
                <br />
                <el-form-item label=":">
                    <span style="font-size: 25px;">{{ state.row1.nickname }}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span style="font-size: 18px;">{{ state.row1.phone }}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span style="font-size: 18px;">{{ state.row1.userid }}</span>
                </el-form-item>
                <hr />
                <h4>*</h4>
                <el-form-item label=" ">
                    <el-input type="text" v-model="name" :ref="el => { form[0] = el }" />
                </el-form-item>
                <el-form-item label="  ">
                    <el-input style="width:90px" v-model="phone" :ref="el => { form[1] = el }" />
                    <label>-</label>
                    <el-input style="width:100px" v-model="phone1" :ref="el => { form[6] = el }" />
                    <label>-</label>
                    <el-input style="width:100px" v-model="phone2" :ref="el => { form[7] = el }" />
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" id="postcode" v-model="postcode" placeholder="" style="width: 150px;"
                        :ref="el => { form[2] = el }" />
                    <el-input type="button" value=" " style="width: 150px;" @click="search()" />
                    <el-input type="text" id="roadAddress" v-model="address" placeholder=""
                        :ref="el => { form[3] = el }" />
                    <el-input type="text" id="jibunAddress" placeholder="" />
                    <span id="guide" style="color:#000;display:none"></span>
                    <el-input type="text" id="extraAddress" placeholder="" />
                    <el-input type="text" id="detailAddress" v-model="detailaddress" placeholder=""
                        :ref="el => { form[4] = el }" />
                </el-form-item>
                <el-form-item label=" ">
                    <el-select v-model="state.comment" placeholder="   " style="width: 250px;"
                        :ref="el => { form[5] = el }">
                        <el-option label="" value="" />
                        <el-option label="    ." value="    ." />
                        <el-option label="   ." value="   ." />
                    </el-select>
                </el-form-item>
                <br />
                <hr />
                <h4>*</h4>
                <div class="iteminfo" v-for="obj in state.row" :key="obj">
                    <br />
                    <el-form-item label="">
                        <td style="border: 1px solid; width: 400px;">{{ obj.name }}</td>
                    </el-form-item>
                    <el-form-item label="/">
                        <span style="font-size: 25px; color: brown;">{{ obj.price }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input-number v-model="obj.cnt" style="margin-right:20px;" />
                        <el-button @click="handleCnt(obj.cnt, obj.cno)" style="margin-right:20px"></el-button>
                    </el-form-item>
                    <br />
                    <hr />
                </div>
                <h4>*</h4>
                <el-form-item label=" ">
                    <td style="border: 1px solid; width: 400px;"><span style="font-size: 25px; color: brown;">{{state.total}}</span></td>
                </el-form-item>
                <!-- <el-form-item label="">
                    <el-radio-group>
                        <el-radio label="" value="" />
                        <el-radio label="" value="" />
                    </el-radio-group>
                </el-form-item> -->
            </el-form>
        </div>
        <div class="butt">
            <el-button round @click="handleCart()" type="info" plain
                style="height: 40px; margin-right: 200px; font-size: 25px; font-weight: 600; color: black;"> 
            </el-button>
            
            <img src="../../assets/pay/KGinicis.png" @click="iamportPay('html5_inicis')" style="width: 130px; padding: 10px; cursor:pointer;" />
            <img src="../../assets/pay/payment_icon_yellow_medium.png" @click="iamportPay('kakaopay')" style="width: 130px; padding: 10px; cursor:pointer;" />
            <img src="../../assets/pay/logo-toss-pay.png" @click="iamportPay('tosspay')" style="width: 130px; padding: 10px; cursor:pointer;" />
            
            <!-- <el-button round @click="iamportPay('html5_inicis')" type="primary" -->
                <!-- style="height: 40px; font-size: 25px; font-weight: 600;"></el-button> -->
            <!-- <el-button round @click="iamportPay('kakaopay')" type="primary" -->
                <!-- style="height: 40px; font-size: 25px; font-weight: 600;"></el-button> -->
            <!-- <el-button round @click="iamportPay('tosspay')" type="primary" -->
                <!-- style="height: 40px; font-size: 25px; font-weight: 600;"></el-button> -->
                
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { onMounted } from '@vue/runtime-core'
import { reactive, ref, toRefs } from '@vue/reactivity';
import { useRoute, useRouter } from 'vue-router';

export default {
    setup() {
        const router = useRouter();
        const route = useRoute();
        const form = ref([]);

        const state = reactive({
            token: sessionStorage.getItem("token"),
            cno: [],
            // cno     : route.query.cno,
            row: '',
            row1: '',
            name: '',
            phone: '',
            phone1: '',
            phone2: '',
            postcode: '',
            address: '',
            detailaddress: '',
            comment: '',
            cnt: '',
            ino: '',
            itemprice: [],
            total: '',
            ordercode: '', // DB    (ordercode)
            orderret: false

        });

        // 
        const handleData = async () => {
            let url = `/fligent/api/order/selectitem.json`;
            const headers = {
                "Content-Type": "application/json",
                "token": state.token
            };
            var params = new URLSearchParams();
            for (let i = 0; i < state.cno.length; i++) {
                params.append("cno", state.cno[i]);
            }
            const { data } = await axios.get(url, { params: params, headers: headers });
            if (data.status === 200) {
                state.row = data.item;
                for (let i = 0; i < state.row.length; i++) {
                    state.itemprice[i] = state.row[i].tot
                }
                state.total = 0;
                for (let i = 0; i < state.itemprice.length; i++) {
                    state.total += state.itemprice[i];
                }
            }
        };

        // 
        const handleData1 = async () => {
            const url = `/fligent/api/order/selectmemberone.json`;
            const headers = {
                "Content-Type": "application/json",
                "TOKEN": state.token
            };
            const { data } = await axios.get(url, { headers });

            if (data.status === 200) {
                state.row1 = data.member
            }
        };

        //  
        const handleAddress = async () => {
            const url = `/fligent/api/order/insertcustomeraddress.json`;
            const headers = {
                "Content-Type": "application/json",
                "token": state.token
            };
            const body = {
                name: state.name,
                phone: `${state.phone}-${state.phone1}-${state.phone2}`,
                postcode: state.postcode,
                address: state.address,
                comment: state.comment,
                detailaddress: state.detailaddress
            }
            const { data } = await axios.post(url, body, { headers });
            // console.log(data);
            if (data.status == 200) {
                state.orderret = true;
            } else {
                alert('!  ')
            }
        };

        //   
        const handleCnt = async (cnt, obj) => {
            const url = `/fligent/api/order/updatecnt.json`;
            const headers = {
                "Content-Type": "application/json",
                "token": state.token
            };
            const body = {
                cno: obj,
                cnt: cnt
            }
            const { data } = await axios.put(url, body, { headers });
            // console.log(data);
            if (data.status === 200) {
                alert(' .')
                handleData();
            } else {
                alert(' .')
            }
        };

        // 
        const handleOrder = async () => {
            // if (state.name === '') {
            //     alert('   .')
            //     form.value[0].focus();
            //     return false;
            // }
            // if (state.phone === '') {
            //     alert('   .')
            //     form.value[1].focus();
            //     return false;
            // }
            // if (state.phone1 === '') {
            //     alert('   .')
            //     form.value[6].focus();
            //     return false;
            // }
            // if (state.phone2 === '') {
            //     alert('   .')
            //     form.value[7].focus();
            //     return false;
            // }
            // if (state.postcode === '') {
            //     alert(' .')
            //     form.value[2].focus();
            //     return false;
            // }
            // if (state.address === '') {
            //     alert(' .')
            //     form.value[3].focus();
            //     return false;
            // }
            // if (state.detailaddress === '') {
            //     alert(' .')
            //     form.value[4].focus();
            //     return false;
            // }
            // if (state.comment === '') {
            //     alert(' .')
            //     form.value[5].focus();
            //     return false;
            // }
            
            const url = `/fligent/api/order/insertbatchorder.json`;
            const headers = {
                "Content-Type": "application/json",
                "token": state.token
            };
            let body = []
            for (let i = 0; i < state.row.length; i++) {
                body.push({
                    "cno": state.row[i].cno,
                    "cnt": state.row[i].cnt,
                    "ino": state.row[i].ino
                });
            }
            const {data} = await axios.post(url, body, { headers });

            if (data.status === 200) {
                state.ordercode = data.ordercode
                await handleAddress();
                if (state.orderret === true) {
                    // router.push({path:'/ordersuccess'});
                }
            }
            else {
                alert(' .');
            }
        };

        onMounted(() => {
            if (Array.isArray(route.query.cno)) {
                for (let i = 0; i < route.query.cno.length; i++) {
                    state.cno.push(route.query.cno[i])
                }
            }
            else {
                state.cno.push(route.query.cno)
            }
            handleData();
            handleData1();

            let scripts = [
                //   
                "//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js",
                //    
                "https://code.jquery.com/jquery-1.12.4.min.js",
                //  
                "https://cdn.iamport.kr/js/iamport.payment-1.2.0.js"
            ];
            for (let i = 0; i < scripts.length; i++) {
                let script = document.createElement('script');
                script.setAttribute('type', 'text/javascript');
                script.setAttribute('src', scripts[i]);
                script.async = true;
                document.getElementsByTagName('head')[0].appendChild(script);
            }

        });

        //   
        const search = () => {
            new window.daum.Postcode({
                oncomplete: (data) => { //function  =>  .
                    //        .

                    //       .
                    //      ('') ,    .
                    var roadAddr = data.roadAddress; //   
                    var extraRoadAddr = ''; //   

                    //    . ( )
                    //     "//" .
                    if (data.bname !== '' && /[||]$/g.test(data.bname)) {
                        extraRoadAddr += data.bname;
                    }
                    //  ,   .
                    if (data.buildingName !== '' && data.apartment === 'Y') {
                        extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
                    }
                    //    ,     .
                    if (extraRoadAddr !== '') {
                        extraRoadAddr = ' (' + extraRoadAddr + ')';
                    }

                    //      .
                    document.getElementById('postcode').value = data.zonecode;
                    document.getElementById("roadAddress").value = roadAddr;
                    document.getElementById("jibunAddress").value = data.jibunAddress;

                    // api       
                    state.postcode = data.zonecode;
                    state.address = roadAddr;

                    //       .
                    if (roadAddr !== '') {
                        document.getElementById("extraAddress").value = extraRoadAddr;
                    } else {
                        document.getElementById("extraAddress").value = '';
                    }

                    var guideTextBox = document.getElementById("guide");
                    //  ' '  ,    .
                    if (data.autoRoadAddress) {
                        var expRoadAddr = data.autoRoadAddress + extraRoadAddr;
                        guideTextBox.innerHTML = '(   : ' + expRoadAddr + ')';
                        guideTextBox.style.display = 'block';

                    } else if (data.autoJibunAddress) {
                        var expJibunAddr = data.autoJibunAddress;
                        guideTextBox.innerHTML = '(   : ' + expJibunAddr + ')';
                        guideTextBox.style.display = 'block';
                    } else {
                        guideTextBox.innerHTML = '';
                        guideTextBox.style.display = 'none';
                    }
                }
            }).open();
        }


        //  API() 
        const iamportPay = async (paycode) => {
            //    
            if (state.name === '') {
                alert('   .')
                form.value[0].focus();
                return false;
            }
            if (state.phone === '') {
                alert('   .')
                form.value[1].focus();
                return false;
            }
            if (state.phone1 === '') {
                alert('   .')
                form.value[6].focus();
                return false;
            }
            if (state.phone2 === '') {
                alert('   .')
                form.value[7].focus();
                return false;
            }
            if (state.postcode === '') {
                alert(' .')
                form.value[2].focus();
                return false;
            }
            if (state.address === '') {
                alert(' .')
                form.value[3].focus();
                return false;
            }
            if (state.detailaddress === '') {
                alert(' .')
                form.value[4].focus();
                return false;
            }
            if (state.comment === '') {
                alert(' .')
                form.value[5].focus();
                return false;
            }
            

            // console.log('paycode => ', paycode)
            // await handleOrder();
            // console.log('  true ? => ',state.orderret)

            // if (state.orderret === true) {
                var IMP = window.IMP;
                IMP.init("imp54605428");
                    // console.log('=> ', state.ordercode)
                    // console.log(' => ', state.name)
                    // console.log('  => ', state.total)
                    // console.log(' => ', state.row1.phone)
                    // console.log(' => ', state.address + state.detailaddress)
                    // console.log(' => ', state.postcode)
                // IMP.request_pay(param, callback) // 
                IMP.request_pay({ //param
                    pg: paycode,
                    pay_method: "card",
                    merchant_uid: state.ordercode,
                    name: state.name,
                    amount: state.total,
                    buyer_email: state.row1.userid,
                    buyer_name: state.name,
                    buyer_tel: state.row1.phone,
                    buyer_addr: state.address + state.detailaddress,
                    buyer_postcode: state.postcode,
                    popup : true
                }, 
                async function (rsp) { // callback
                    //     
                    // console.log(' ===> ', window.IMP.request_Pay)

                    if (rsp.success == true) {
                        await handleAddress();
                        await handleOrder();

                        if(state.orderret === true){
                            // console.log('')
                            var msg = ' .';
                            // msg += 'ID : ' + result_success.imp_uid;
                            // msg += ' ID : ' + result_success.merchant_uid;
                            // msg += '  : ' + result_success.paid_amount;
                            // msg += '  : ' + result_success.apply_num;
                            alert(msg);
                            router.push({path:'/ordersuccess'});
                        }
                        else 
                        {
                            alert(' .  ');
                            router.push({path:'/home'});
                        }
                    }
                    //    
                    else {
                        var msg1 = ' .';
                        msg1 += ' : ' + rsp.error_msg;
                        alert(msg1);
                        router.push({path:'/cart'});
                        // router.go();
                    }
                })
                window.IMP
            // }
        }


        // const tossPay = async (paycode) => {
        //     console.log('paycode => ', paycode)
        //     console.log('11111111111111')
        //     await handleOrder();
        //     console.log('  true ? => ',state.orderret)
        //     if (state.orderret === true) {
        //         // console.log('22222222222')
        //         var IMP = window.IMP;
        //         IMP.init("imp54605428");
        //             console.log('=> ', state.ordercode)
        //             console.log(' => ', state.name)
        //             console.log('  => ', state.total)
        //             console.log(' => ', state.row1.phone)
        //             console.log(' => ', state.address + state.detailaddress)
        //             console.log(' => ', state.postcode)
        //         // IMP.request_pay(param, callback) // 
        //         IMP.request_pay({ //param
        //             pg: paycode,
        //             pay_method: "card",
        //             merchant_uid: state.ordercode,
        //             name: state.name,
        //             amount: state.total,
        //             buyer_email: state.row1.userid,
        //             buyer_name: state.name,
        //             buyer_tel: state.row1.phone,
        //             buyer_addr: state.address + state.detailaddress,
        //             buyer_postcode: state.postcode,
        //             // m_redirect_url : '{http://localhost:9090/}' 
        //             m_redirect_url : '{http://localhost:9090/ordersuccess}' 
        //             // m_redirect_url : router.push({path:'/ordersuccess'})
        //         }, 
        //         function (rsp) { // callback
        //             //     
        //             //   ! m_redirect_url 
        //             // console.log(' ===> ', window.IMP.request_Pay)

        //             console.log('!!!!!!!!!!!11', rsp)
        //             if (rsp.success == true) {
        //                 var msg = ' .';
        //                 // msg += 'ID : ' + result_success.imp_uid;
        //                 // msg += ' ID : ' + result_success.merchant_uid;
        //                 // msg += '  : ' + result_success.paid_amount;
        //                 // msg += '  : ' + result_success.apply_num;
        //                 alert(msg);
        //                 router.push({path:'/ordersuccess'});
        //             }
        //             //    
        //             else {
        //                 var msg1 = ' .';
        //                 msg1 += ' : ' + rsp.error_msg;
        //                 alert(msg1);
        //             }
        //         })
        //         window.IMP
        //     }
        // }

        const handleCart = () => {
            router.push({ path: '/cart' });
        };

        return {
            form,
            state,
            ...toRefs(state),
            handleCart,
            handleOrder,
            search,
            handleCnt,
            iamportPay,
            // tossPay
        }
    }
}
</script>

<style lang="css" scoped>
.container {
    /* border:1px solid #cccccc; */
    text-align: center;
    width: 80%;
}

.order {
    /* border:1px solid #cccccc; */
    text-align: center;
    width: 100%;
}

.order h4 {
    font-weight: 600;
    margin-top: 30px;
    margin-bottom: 30px;
}

.butt {
    width: 100%;
    /* border:1px solid #cccccc; */
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 150px;
}

.container h3 {
    font-family: 'MapoFlowerIsland';
    font-weight: 600;
    font-size: 50px;
    margin-bottom: 50px;
    margin-top: 50px;
}
